*,
*:before,
*:after {
  box-sizing: border-box;
}

html {
  font-size: 8px;
}

@font-face {
  font-family: 'Digital';
  src: local('Digital'),  url('./fonts/digital-7.woff') format('woff');
}

body {
  font-family: monospace;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
